.wpo_hidden {
	display: none;
}

/* big button */
.wpo_primary_big {
	padding: 4px 6px !important;
	font-size: 22px !important;
	min-height: 34px;
	min-width: 200px;
}

/*  SECTIONS  */
.wpo_section {
	clear: both;
	padding: 0;
	margin: 0;
}

.wp-optimize-settings {
	margin-bottom: 16px;
}

/*  COLUMN SETUP  */
.wpo_col {
	display: block;
	float: left;
	margin: 1% 0 1% 1%;
}

.wpo_col:first-child {
	margin-left: 0;
}

/*  GROUPING  */
.wpo_group:before,
.wpo_group:after {
	content: "";
	display: table;
}

.wpo_group:after {
	clear: both;
}

.wpo_half_width {
	width: 48%;
}

/*  GRID OF THREE  */
.wpo_span_3_of_3 {
	width: 100%;
}

.wpo_span_2_of_3 {
	width: 65.3%;
}

.wpo_span_1_of_3 {
	width: 32.1%;
}

.nav-tab-wrapper {
	margin: 14px 0px;
}

@media screen and (min-width: 549px) {

	.show_on_default_sizes {
		display: block !important;
	}

	.show_on_mobile_sizes {
		display: none !important;
	}

}

@media screen and (max-width: 548px) {

	.show_on_default_sizes {
		display: none !important;
	}

	.show_on_mobile_sizes {
		display: block !important;
	}

}

@media screen and (max-width: 768px) {

	.wpo_col {
		margin: 1% 0;
	}

	.wpo_span_3_of_3 {
		width: 100%;
	}

	.wpo_span_2_of_3 {
		width: 100%;
	}

	.wpo_span_1_of_3 {
		width: 100%;
	}

	.wpo_half_width {
		width: 100%;
	}

}

.wp-optimize-settings-clean-transient label, .wp-optimize-settings-clean-pingbacks label, .wp-optimize-settings-clean-trackbacks label, .wp-optimize-settings-clean-postmeta label, .wp-optimize-settings-clean-orphandata label, .wp-optimize-settings-clean-commentmeta label {
	color: #9B0000;
}

td.wp-optimize-settings-optimization-checkbox {
	width: 18px;
	padding-left: 4px;
	padding-right: 0px;
}

.wp-optimize-settings-optimization-checkbox input {
	margin: 0px;
	padding: 0px;
}

#retention-period {
	width: 60px;
}

.wp-optimize-settings-optimization-info {
	font-size: 80%;
	padding-left: 32px;
	font-style: italic;
}

.wp-optimize-settings input[type="checkbox"] {
	width: 18px;
}

/* Added for the Image on Addons tab*/
img.addons {
	display: block;
	margin-left: auto;
	margin-right: auto;
	height: 44px;
}

.wpo_spinner {
	width: 18px;
	height: 18px;
	padding-left: 10px;
	display: none;
	position: relative;
	top: 4px;
}

.optimization_spinner {
	width: 20px;
	height: 20px;
}

#wp-optimize-auto-options {
	margin: 20px 0 0 28px;
}

.display-none {
	display: none;
}

.visibility-hidden {
	visibility: hidden;
}

.margin-one-percent {
	margin: 1%;
}

#save_done {
	color: #D94F00;
	font-size: 250%;
}

.wp-optimize-settings-optimization-info a {
	text-decoration: underline;
}

.wp-optimize-settings-optimization-run-spinner {
	position: relative;
	top: 2px;
}

#wpoptimize_table_list .tablesorter-filter-row {
	display: none !important;
}

#wp_optimize_table_list_refresh {
	float: right;
}

#wp_optimize_table_list_refresh:hover {
	cursor: pointer;
}

#wpoptimize_table_list .optimization_spinner {
	position: relative;
	top: 5px;
	left: 5px;
}

#wpoptimize_table_list_filter {
	width: 100%;
}

#wpoptimize_table_list_tables_not_found {
	display: none;
}

#optimize_form .select2-container,
#wp-optimize-auto-options .select2-container {
	width: 50% !important;
	top: -5px;
	height: 40px;
	margin-left: 10px;
}

#wpoptimize_table_list .optimization_done_icon {
	color: #D94F00;
	font-size: 200%;
	display: inline-block;
	position: relative;
	top: 5px;
	left: -20px;
}

#wpo_sitelist_show_moreoptions_cron {
	font-size: 13px;
	line-height: 1.5;
	letter-spacing: 1px;
}

.wpo_unused_image {
	position: relative;
	float: left;
	margin: .5%;
	width: 32%;
	text-align: center;
	background: rgba(220, 220, 220, 0.5);
	height: 20vh;
	min-height: 200px;
}

.wpo_unused_image img {
	max-height: 80%;
	max-width: 90%;
	margin: 10px 5px;
}

.wpo_unused_image_checkbox_conatiner {
	text-align: center;
	position: absolute;
	width: 100%;
	bottom: 7px;
}

#wp-optimize-nav-tab-contents-images .wpo_span_2_of_3 h3 {
	display: inline-block;
}

#wpo_remove_unused_images_btn {
	margin-top: 10px;
	margin-right: 10px;
	float: right;
}

#wpo_remove_selected_sizes_btn {
	margin-top: 20px;
}

#wp-optimize-nav-tab-contents-tables a {
	vertical-align: middle;
}

#wpo_sitelist_moreoptions,
#wpo_sitelist_moreoptions_cron {
	margin: 4px 16px 6px 0;
	border: 1px dotted;
	padding: 6px 10px;
	max-height: 300px;
	overflow-y: scroll;
	overflow-x: hidden;
}

#wpo_sitelist_moreoptions {
	max-height: 150px;
	margin-right: 0;
}

#wpo_settings_sites_list li,
#wpo_settings_sites_list li a {
	font-size: 13px;
	line-height: 1.5;
}

#wpo_sitelist_moreoptions_cron li {
	padding-left: 20px;
}

#wpo_import_error_message {
	display: none;
	color: #9B0000;
}

#wpo_import_success_message {
	display: none;
	color: #46B450;
}

#wp-optimize-logging-options {
	margin-top: 10px;
}

/* Logger settings*/
.wpo_logging_header {
	font-weight: bold;
	border-top: 1px solid #333;
	border-bottom: 1px solid #333;
	padding: 5px 0;
	margin: 0;
}

.wpo_logging_row {
	border-bottom: 1px solid #A1A2A3;
	padding: 5px 0;
}

.wpo_logging_logger_title,
.wpo_logging_options_title,
.wpo_logging_status_title,
.wpo_logging_actions_title,
.wpo_logging_logger_row,
.wpo_logging_options_row,
.wpo_logging_status_row,
.wpo_logging_actions_row {
	display: inline-block;
}

.wpo_logging_logger_title,
.wpo_logging_logger_row {
	width: 38%;
}

.wpo_logging_options_title,
.wpo_logging_options_row {
	width: 44%;
}

.wpo_logging_status_title,
.wpo_logging_status_row {
	width: 8%;
}

.wpo_logging_actions_title,
.wpo_logging_actions_row {
	width: 7%;
}

.wpo_logging_actions_row {
	text-align: right;
}

.wpo_logging_options_row {
	word-wrap: break-word;
}

#wpo_add_logger_link {
	clear: both;
	display: block;
	cursor: pointer;
	font-weight: bold;
}

.wpo_logging_actions_row .dashicons-no-alt,
.wpo_add_logger_form .dashicons-no-alt {
	background-color: #F06666;
	color: #FFF;
	width: 20px;
	height: 20px;
	border-radius: 20px;
	display: inline-block;
	cursor: pointer;
	margin-left: 5px;
}

.wpo_add_logger_form .dashicons-no-alt {
	margin-top: 12px;
	margin-right: 10px;
	float: right;
}

.wpo_logger_type {
	width: 90%;
	margin-top: 10px;
}

.wpo_logger_addition_option {
	width: 100%;
	margin-top: 5px;
}

.wpo_alert_notice {
	background-color: #F06666;
	color: #FFF;
	padding: 5px;
	display: block;
	margin-bottom: 5px;
	border-radius: 5px;
}

.wpo_error_field {
	border-color: #F06666 !important;
}

.save_settings_reminder {
	display: none;
	color: #333;
	padding: 5px 10px;
	border: 1px solid #F00;
	background-color: #F0A5A4;
	border-radius: 5px;
	margin: 15px 0;
}

/* Added for WPO Premium Features tab */
.wpo_feature_cont {
	width: 54.5%;
}

.wpo_plugin_family_cont {
	width: 44.5%;
}

.wpo_feat_table, .wpo_feat_th, .wpo_feat_table td {
	border: 1px solid black;
	border-collapse: collapse;
	font-size: 120%;
	background-color: white;
	text-align: center;
}

.wpo_feat_table p {
	padding: 0px 10px;
	margin: 5px 0px;
	font-size: 16px;
}

.wpo_feat_table h4 {
	margin: 5px 0px;
}

.wpo_feat_table .dashicons {
	width: 25px;
	height: 25px;
	font-size: 25px;
	line-height: 1;
}

.wpo_feat_table .dashicons-yes, .wpo_feat_table .updraft-yes {
	color: green;
}

.wpo_feat_table .dashicons-no-alt, .wpo_feat_table .updraft-no {
	color: red;
}

.wpo-premium-image {
	display: none;
}

@media screen and (min-width: 720px) {

	#wpoptimize_table_list_filter {
		width: 40%;
	}

	.wpo-premium-image {
		display: block;
		float: left;
		padding: 14px 8px;
		width: 50px;
		height: 50px;
	}

}

.other-plugin-title {
	text-decoration: none;
}

#wpo_remove_selected_sizes {
	margin-top: 20px;
}

.wpo_unused_images_buttons_wrap {
	display: none;
}

.wpo_unused_images_container h3 {
	min-width: 150px;
}

#wpo_unused_images {
	max-height: 500px;
	overflow-y: auto;
}

#wpo_unused_images a {
	outline: none;
}